html {
  scroll-behavior: smooth;
  cursor: default;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: #173591;
  background-color: #f5f5f5;
  font-family: 'JetBrains Mono', 'Segoe UI', 'Roboto', 'Oxygen', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.material-icons {
  font-size: 1rem;
  pointer-events: none;
  /* TODO: Fix widget movement */
}

button.icon-desc span.material-icons {
  margin-right: 4px;
}

button.btn-no-margin {
  margin: 0 0 0 0;
  height: 30px;
}

button.btn-margin-top {
  margin-top: 10px;
}

.rainbow {
  animation: subtle-animation 3s ease-in-out 0s infinite normal;
}

#sheet-header {
  width: 100%;
  overflow-x: auto;
}

summary {
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  margin-bottom: 5px;
}

summary:hover {
  color: #537CD1;
}

hr {
  border: 0;
  border-top: 2px solid #173591;
}

.markdown-text p {
  white-space: pre-wrap;
}

a {
  text-decoration: underline dotted currentColor;
  transition: all 0.1s;
}

a.alias-pointer {
  cursor: alias;
}

li a {
  text-decoration: none;
}

a, a:visited {
  color: #1B44C1;
}

a:hover, a:focus {
  color: #7BABE5;
}


input {
  padding: 15px;
  border: none;
  border-radius: 2px;
  background-color: rgb(231, 231, 231);
  color: #173591;
  font-weight: bold;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 10px;
}

input:focus {
  outline: none;
  border: none;
}

input[type=submit] {
  background-color: #173591;
  color: white;
  cursor: pointer;
  user-select: none;
  font-family: inherit;
  box-shadow: 0 3px 1px -2px rgba(0,0,0,.2),0 2px 2px 0 rgba(0,0,0,.14),0 1px 5px 0 rgba(0,0,0,.12); /* stolen from Angular Material */
}

button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  flex-wrap: nowrap;
  flex-direction: row;
  padding: 10px;
  background-color: #173591;
  color: white;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  box-shadow: 0 3px 1px -2px rgba(0,0,0,.2),0 2px 2px 0 rgba(0,0,0,.14),0 1px 5px 0 rgba(0,0,0,.12);
  font-family: inherit;
  margin-bottom: 10px;
}

textarea {
  width: 100%;
  box-sizing: border-box;
  border: none;
  background-color:rgb(231, 231, 231);
  font-family: inherit;
  padding: 10px;
  color: #173591;
  border-radius: 5px;
  resize: vertical;
}

textarea:hover {
  outline: none;
}

label {
  font-weight: bold;
  margin-bottom: 10px;
}

select {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  color: #173591;
  border: none;
  border-radius: 5px;
  background-color:rgb(231, 231, 231);
}

.fullbutton {
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 10px;
}

.animated-button {
  animation: button-in .2s 1;
}

.item-list {
  width: 100%;
  border-collapse: collapse;
}

.item-list thead {
  background-color: white;
  font-weight: normal;
}

th.center, td.center {
  text-align: center;
}

th.left, td.left {
  text-align: left;
}

th.right, td.right {
  text-align: right;
}

.item-list thead tr {
  padding: 10px;
  font-size: 13px;
  color: dimgray;
  font-weight: normal;
}

.item-list thead tr th {
  padding: 10px 20px;
  font-weight: normal;
}

.item-list tr td {
  background-color: white;
  border-top: 1px solid lightgray;
  padding: 5px;
  cursor: pointer;
  color: black;
  font-weight: bold;
  padding: 5px 20px 5px 20px;
}

.card {
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 3px 1px -2px rgba(0,0,0,.2),0 2px 2px 0 rgba(0,0,0,.14),0 1px 5px 0 rgba(0,0,0,.12);
  margin-bottom: 10px;
  padding: 10px;
}

.flat-button {
  color: #173591;
  border: 2px solid lightgray;
  border-radius: 5px;
  box-shadow: none;
  font-family: inherit;
  padding: 10px;
  font-weight: bold;
  background-color: transparent;
  margin-bottom: 10px;
}

.widget {
  position: relative;
}

.widget-editbar {
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px;
  display: none;
  background-color: lightgray;
}

.widget-editbar .flat-button {
  margin-bottom: 0px;
}

.widget:hover .widget-editbar {
  display: block;
}



@keyframes button-in {
  0% {
    opacity: 0;
  }


  100% {
    opacity: 1;
  }
}

@keyframes rainbow-color {
  0% {
    color: #0114E6;
  }
  12.5% {
    color: #00CFE6;
  }
  25% {
    color: #00E649;
  }
  37.5% {
    color: #84E600;
  }
  50% {
    color: #E6B900;
  }
  62.5% {
    color: #E66B00;
  }
  75% {
    color: #E60501;
  }
  77.5% {
    color: #BD01E6;
  }
  100% {
    color: #0114E6;
  }
}


@keyframes subtle-animation {
  0% {
    color: #1B44C1;
  }
  50% {
    color: #607bc5;
  }
  100% {
    color: #1B44C1;
  }
}